@charset "utf-8";

.intro {
  min-height: 658px;
  padding-top: 30px;
  background:
    conic-gradient(
    from 130.31deg at 16.03% 39.06%,
    $color-venetian-red -46.69deg,
    $color-dark-cerulean 32.24deg,
    $color-venetian-red 313.31deg,
    $color-dark-cerulean 392.24deg);

  @include vp-1199 {
    min-height: 554px;
    padding-top: 40px;

    background: conic-gradient(
      from 139.4deg at 20.7% 35.74%,
      $color-venetian-red -46.69deg,
      $color-dark-cerulean 32.24deg,
      $color-venetian-red 313.31deg,
      $color-dark-cerulean 392.24deg);
  }

  @include vp-767 {
    min-height: 491px;
    padding-top: 20px;

    background: conic-gradient(
      from 171.43deg at 35.31% 34.22%,
      $color-venetian-red -46.69deg,
      $color-dark-cerulean 32.24deg,
      $color-venetian-red 313.31deg,
      $color-dark-cerulean 392.24deg);
  }
}

.intro__header {
  margin-top: -2px;
  margin-bottom: 2px;
  display: flex;
  justify-content: space-between;

  @include vp-1199 {
    margin-bottom: 25px;
  }

  @include vp-767 {
    margin-bottom: 71px;
  }
}

.intro__logo {
  width: 89px;
  height: 20px;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:focus {
      outline: 3px solid $color-resolution-blue;
      outline-offset: 7px;
    }
  }

  @include vp-1199 {
    width: 82px;
    height: 17px;
  }

  @include vp-767 {
    width: 68px;
    height: 15px;
  }
}

.intro__tel {
  text-transform: uppercase;
  font: 700 26px/120% $ff-bebas-neue;
  color: $color-default-white;
  letter-spacing: 0.03em;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.7;
    }

    &:focus {
      outline: 3px solid $color-resolution-blue;
      outline-offset: 7px;
    }
  }

  @include vp-1199 {
    font-size: 24px;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    font-size: 20px;
  }
}

.intro__content {
  display: flex;
}

.intro__heading {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  min-width: 377px;
  margin-left: 20px;
  margin-right: 62px;

  & > p:first-child {
    margin: 0 0 7px 0;
    padding: 0;

    font: 400 49px/120% $ff-bebas-neue;
    letter-spacing: 0.03em;
    color: $color-default-white;
  }

  @include vp-1199 {
    margin-left: 90px;
    margin-right: 0;
    min-width: auto;

    & > p:first-child {
      font-size: 34px;
      margin-bottom: 4px;
    }
  }

  @include vp-767 {
    margin-left: 0;
    width: 100%;
    justify-content: flex-start;

    & > p:first-child {
      font-size: 28px;
      margin-bottom: 9px;
    }
  }
}

h1 {
  margin: 0 0 50px 0;
  padding: 0;

  font: 700 110px/100% $ff-bebas-neue;
  letter-spacing: 0.03em;
  color: $color-default-white;
  text-shadow: 2px 0 2px $color-default-white;
  text-align: right;

  @include vp-1199 {
    font-size: 86px;
    margin-bottom: 36px;
  }

  @include vp-767 {
    font-size: 78px;
    margin-bottom: 37px;
  }
}

.intro__features {
  margin: 0;
  padding: 0;
  list-style: none;

  position: absolute;
  bottom: 34.3%;
  left: 0;

  font: 400 14px/150% $ff-roboto-condensed;
  letter-spacing: 0;
  color: $color-default-white;
  text-align: right;
  text-shadow: none;

  @include vp-1199 {
    bottom: 37%;
    left: -90px;
  }

  @include vp-767 {
    bottom: 39%;
    left: 0;
    right: 177px;
  }
}

.intro__btn {
  margin-bottom: 43px;
  width: 234px;

  @include vp-1199 {
    margin-bottom: 23px;
    width: 185px;
  }

  @include vp-767 {
    width: 100%;
    margin-bottom: 0;
    padding-top: 21px;
    padding-bottom: 21px;
  }
}

.intro__image {
  margin-top: -10px;
  vertical-align: middle;
  flex-shrink: 0;

  img {
    object-fit: cover;
    width: 500px;
    height: 556px;
  }

  @include vp-1199 {
    margin-left: 30px;

    img {
      width: 324px;
      height: 390px;
    }
  }

  @include vp-767 {
    display: none;
  }
}

.intro__socials {
  align-self: flex-end;
  margin: 0 0 38px auto;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: column;

  li:not(:last-child) {
    margin-bottom: 9px;
  }

  li:last-child {
    position: relative;
    left: 3px;
  }

  @include vp-1199 {
    display: none;
  }
}

.intro__link {
  font: 400 12px/120% $ff-roboto-condensed;
  color: $color-default-white;
  text-transform: uppercase;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      text-decoration: underline;
    }

    &:focus {
      outline: 3px solid $color-resolution-blue;
      outline-offset: 7px;
    }
  }
}
